<template>
  <div class="container">
    <div class="section1" v-if="show">
      <div class="add-item" @click="addItem">
        <van-icon name="add-o" size="30" color="#1989fa" />
      </div>
      <div class="item" v-for="(item, index) in itemList" :key="index">
        <van-cell-group>
          <van-field 
            v-model="item.name"
            label="名称"
            placeholder="请输入名称"
          />
          <van-field
            v-model="item.material"
            label="材质"
            placeholder="请输入材质"
          />
          <van-field
            v-model="item.number"
            label="数量"
            placeholder="请输入数量"
          />

          <van-field
            v-model="item.remark"
            rows="2"
            autosize
            label="备注"
            type="textarea"
            maxlength="50"
            placeholder="请输入备注"
            show-word-limit
          />
          <div class="flex-sa">
            <van-field v-model="item.length" placeholder="请输入长度" />
            <van-field v-model="item.width" placeholder="请输入宽度" />
          </div>
        </van-cell-group>
        <div @click="savePro(index)" class="save-item">
          {{ item.id != "" ? "修改" : "保存" }}
        </div>
      </div>

      <div class="add-btn">
        <van-button size="normal" @click="switchStatus" block round type="info"
          >添加完成,下一步</van-button
        >
      </div>
    </div>
    <div class="section2" v-if="!show">
      <div class="item">
        <van-collapse v-model="activeNames">
          <van-collapse-item
            :title="sitem.name"
            :name="sindex"
            v-for="(sitem, sindex) in saveItemList"
            :key="sindex"
          >
            <van-cell
              center
              :title="item.pname"
              v-for="(item, index) in sitem.processList"
              :key="index"
            >
              <template #right-icon>
                <van-switch
                  :value="item.checked"
                  @input="onInput(item.checked, sindex, index)"
                  size="24"
                />
              </template>
            </van-cell>
          </van-collapse-item>
        </van-collapse>
      </div>
      <!-- <div class="item">
        <van-field name="radio" label="复用第一个">
          <template #input>
            <van-radio-group v-model="radio" direction="horizontal">
              <van-radio name="1">是</van-radio>
              <van-radio name="2">否</van-radio>
            </van-radio-group>
          </template>
        </van-field>
      </div> -->

      <div class="add-btn">
        <van-button @click="switchStatus" size="normal" block round type="info"
          >返回上一步</van-button
        >
        <van-button @click="subPro" size="normal" block round type="info"
          >提交</van-button
        >
      </div>
    </div>
  </div>
</template>

<script>
import { addPorder, getPro, addPcart } from "@api/public";
import {
  Button,
  RadioGroup,
  Switch,
  Cell,
  Radio,
  Collapse,
  
  Field,
  CollapseItem,
  Icon,
} from "vant";
import localStorage from "@utils/store/localStorage";
import Loadings from "@components/Loadings";
export default {
  name: "Index",
  components: {
    [Button.name]: Button,
    [CollapseItem.name]: CollapseItem,
    [Cell.name]: Cell,
    [Collapse.name]: Collapse,
    [RadioGroup.name]: RadioGroup,
    [Radio.name]: Radio,
    [Switch.name]: Switch,
    [Field.name]: Field,
    [Icon.name]: Icon,
    Loadings,
  },
  props: {},
  data: function () {
    return {
      itemList: [
        {
          name: "",
          material: "",
          number: "",
          length: "",
          width: "",
          remark: "",
          id: "",
        },
      ],
      saveItemList: [],
      processList: [],
      activeNames: ["0"],
      show: true,

    };
  },
  created() {
    getPro({})
      .then(({ data }) => {
        data.forEach((item) => {
          item.checked = false;
        });

        this.processList = data;
      })
      .catch((e) => {
        this.$dialog.message(e.msg);
      });
  },
  mounted: function () {},
  methods: {
    savePro(e) {
      if (this.itemList[e]["name"] == "") {
        this.$dialog.message("请输入产品名称");
      } else if (this.itemList[e]["material"] == "") {
        this.$dialog.message(" 请输入产品材质");
      } else if (this.itemList[e]["number"] == "") {
        this.$dialog.message(" 请输入产品数量");
      } else if (this.itemList[e]["width"] == "") {
        this.$dialog.message("请输入产品宽度 ");
      } else if (this.itemList[e]["length"] == "") {
        this.$dialog.message("请输入产品长度 ");
      } else {
        addPorder(this.itemList[e])
          .then(({ data }) => {
            var datas = data;
            datas["processList"] = this.processList;
            this.itemList[e]["id"] = data.id;
            this.saveItemList[e] = datas;
            console.log(this.saveItemList);
            this.$dialog.message("保存成功");
          })
          .catch((e) => {
            this.$dialog.message(e.msg);
          });
      }
    },
    subPro() {
      console.log(this.saveItemList);
      var data  = [];
      this.saveItemList.forEach(item=>{
        var obj = {};
        var arr = [];
        obj['oid'] = item.id;
        item.processList.forEach(sitem=>{
          if(sitem.checked){
            arr.push(sitem.pid);
          }
        })
        obj['proid'] = arr;
        data.push(obj);
      });
  
      addPcart({data})
        .then(({ data }) => {
          this.$dialog.message("提交成功");
          location.reload();
        })
        .catch((e) => {
          this.$dialog.message(e.msg);
        });
    },
    addItem() {
      var list = this.itemList;
      list.push({
        name: "",
        material: "",
        number: "",
        length: "",
        width: "",
        id: "",
        remark: "",
      });

      this.itemList = list;
    },
    switchStatus() {
      if (this.saveItemList.length > 0) {
        this.show = !this.show;
      } else {
        this.$dialog.message("请先保存好添加完成的产品");
      }
    },
    onInput(e, inds, ind) {
      var saveItemList = this.saveItemList;

      saveItemList[inds]["processList"][ind]["checked"] = !e;
      this.saveItemList = saveItemList;
      console.log(e);
      console.log(inds);
      console.log(ind);
      console.log(saveItemList);
    },
  },
};
</script>
<style scoped>
[v-cloak] {
  display: none;
}
button {
  margin-bottom: 15px;
}
.container {
  background: #f9f9f9;
}
