  text-align: center;
  background: #fff;
  box-shadow: 0 0 10px #eee;
  color: #666;
  line-height: 45px;
}
.add-btn {
  margin: 20px;
  margin-top: 40px;
}
.add-item {
  text-align: center;
  padding-top: 10px;
}
